.FirstScreen {
  position: fixed;
  width: 100vw;
  height: 100%;

  animation: fadein;
  animation-duration: 3s;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media (max-width: 450px) {
  .FirstScreen_header {
    height: 12%;
    padding-left: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .FirstScreen_body {
    height: 70%;
    padding: 5%;
  }
  .FirstScreen_body_background {
    height: 100%;
  }
  .FirstScreen_body_title {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .FirstScreen_body_title_inline {
    display: flex;
    align-items: flex-end;
  }
  .FirstScreen_body_picture {
    height: 70%;
    margin-left: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .FirstScreen_picture {
    max-width: 100%;
    max-height: 100%;
  }

  .FirstScreen_footer {
    height: 18%;
    padding-left: 5vw;
    padding-right: 5vw;
    display: flex;
    align-items: center;
  }
  .FirstScreen_button_start {
    height: 12vh;
    border-radius: 1rem;
    font-size: 4vmax;
  }
}
